import { useEffect, FC } from "react";
import { useLocation } from "react-router-dom";

const ScrollToTop: FC<React.PropsWithChildren> = ({ children }) => {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return <>{children}</> || null;
};

export default ScrollToTop;
